import Vue from 'vue';
import VueRouter from 'vue-router';
import Home from '../views/Home.vue';
import Photo from '../views/Photo.vue';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'home',
    component: Home,
  },
  { path: '/photos/:id', component: Photo },
];

const router = new VueRouter({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes,
});

export default router;
